import "./App.css";
import { useEffect, useMemo, useState } from "react";
import Start from "./components/Start";
import Timer from "./components/Timer";
import Quiz from "./components/Quiz";

function App() {
  const [username, setUsername] = useState(null);
  const [timeOut, setTimeOut] = useState(false);
  const [questionNumber, setQuestionNumber] = useState(1);
  const [earned, setEarned] = useState("تومان 0");

  const data = [
    {
      id: 1,
      question: " خالق زبان جاوا اسکریپت کیست",
      answers: [
        {
          text: "James Gosling",
          correct: false,
        },
        {
          text: "Brendan Eich",
          correct: true,
        },
        {
          text: "Dennis Ritchie",
          correct: false,
        },
        {
          text: "Rasmus Lerdorf",
          correct: false,
        },
      ],
    },
    {
      id: 2,
      question: "بهترین بازیکن سال 2019 فوتبال که بود",
      answers: [
        {
          text: "Ronaldo",
          correct: false,
        },
        {
          text: "Modrich",
          correct: false,
        },
        {
          text: "Messi",
          correct: true,
        },
        {
          text: "Neymar",
          correct: false,
        },
      ],
    },
    {
      id: 3,
      question: "دانشمند ایرانی در ناسا؟",
      answers: [
        {
          text: "قاسم حسنی",
          correct: false,
        },
        {
          text: "مجید مجیدی",
          correct: false,
        },
        {
          text: "محسن کریمی",
          correct: false,
        },
        {
          text: "فیروز نادری",
          correct: true,
        },
      ],
    },
  ];

  const moneyPyramid = useMemo(
    () =>
      [
        { id: 1, amount: "1 میلیون" },
        { id: 2, amount: "2 میلیون" },
        { id: 3, amount: "3 میلیون" },
      ].reverse(),
    []
  );

  useEffect(() => {
    questionNumber > 1 &&
      setEarned(moneyPyramid.find((m) => m.id === questionNumber - 1).amount);
  }, [questionNumber, moneyPyramid]);

  return (
    <div className="app">
      {!username ? (
        <Start setUsername={setUsername} />
      ) : (
        <>
          <div className="main">
            {timeOut ? (
              <h1 className="endText">جایزه شما : {earned}</h1>
            ) : (
              <>
                <div className="top">
                  <div className="timer">
                    <Timer
                      setTimeOut={setTimeOut}
                      questionNumber={questionNumber}
                    />
                  </div>
                </div>
                <div className="bottom">
                  <Quiz
                    data={data}
                    questionNumber={questionNumber}
                    setQuestionNumber={setQuestionNumber}
                    setTimeOut={setTimeOut}
                  />
                </div>
              </>
            )}
          </div>
          <div className="pyramid">
            <ul className="moneyList">
              {moneyPyramid.map((m) => (
                <li
                  className={
                    questionNumber === m.id
                      ? "moneyListItem active"
                      : "moneyListItem"
                  }
                >
                  <span className="moneyListItemNumber">{m.id}</span>
                  <span className="moneyListItemAmount">{m.amount}</span>
                </li>
              ))}
            </ul>
          </div>
        </>
      )}
    </div>
  );
}

export default App;
